import { useState } from 'react';

function CalcularT() {
  const [V, SetV] = useState(0);

  const [T, SetT] = useState(0);

  const [X, SetX] = useState(0);

  const calcular = () => {
    return SetT(X / V)
  }

  return (
    <div className="CalcularT">
      <h1>Resultado</h1>
      <h1>T = {T}s</h1>
      <div>
      <h2>Distancia (X)</h2>
      <input type="number" id="X" onChange={(env) => {SetX(env.target.value)}} />m
      <br />
      <h2>Velocidad (V)</h2>
      <input type="number" id="V" onChange={(env) =>{SetV(env.target.value)}} />m/s
      <br />
      <br />
      <button onClick={() => {calcular()}}>CalcularT</ button>
      </div>
      <div>
        <h1>Procedimiento</h1>
        <h3>T = X / V</h3>
        <h3>T = {X}M / {V}M/S</h3>
        <h3>T = {T}S</h3>
      </div>
    </div>
);
}

export default CalcularT;
