import { useState } from 'react';

function CalcularV() {
  const [V, SetV] = useState(0);

  const [T, SetT] = useState(0);

  const [X, SetX] = useState(0);

  const calcular = () => {
    return SetV(X / T)
  }

  return (
    <div className="CalcularV">
      <h1>Resultado</h1>
      <h1>V = {V}m/s2</h1>
      <div>
      <h2>Distancia (X)</h2>
      <input type="number" id="X" onChange={(env) => {SetX(env.target.value)}} />
      <br />
      <h2>Tiempo (T)</h2>
      <input type="number" id="T" onChange={(env) => {SetT(env.target.value)}} />
      <br />
      <br />
      <button onClick={() => {calcular()}}>CalcularV</ button>
      </div>
      <div>
        <h1>Procedimiento</h1>
        <h3>V = X / T</h3>
        <h3>T = {X}M / {T}S</h3>
        <h3>V = {V}M/S</h3>
      </div>
    </div>
  )
}

export default CalcularV;
