import { useState } from 'react';

function CalcularX() {

  const [V, SetV] = useState(0);

  const [T, SetT] = useState(0);

  const [X0, SetX0] = useState(0);

  const [X, SetX] = useState(0);

  const calcular = () => {
    let result = X0 + V * T;
    return SetX(result)
  }

  return (
    <div className="CalcularX">
      <h1>Resultado</h1>
      <h1>X = {X}m</h1>
      <div>
      <h2>Distancia Inicial (X0)</h2>
      <input type="number" id="X0" onChange={(env) => {SetX0(env.target.value)}} />
      <br />
      <h2>Tiempo</h2>
      <input type="number" id="T" onChange={(env) => {SetT(env.target.value)}} />
      <br />
      <h2>Velocidad (V)</h2>
      <input type="number" id="V" onChange={(env) =>{SetV(env.target.value)}} />
      <br />
      <br />
      <button onClick={() => {calcular()}}>CalcularX</ button>
      </div>
      <div>
        <h1>Procedimiento</h1>
        <h3>X = X0 + V * T</h3>
        <h3>X = {X0}M + {V}M/S * {T}S</h3>
        <h3>X = {X}M</h3>
      </div>
    </div>
  );
}

export default CalcularX;
