import { useState } from 'react';
import CalcularX from './calcX';
import CalcularT from './calcT';
import CalcularV from './calcV';

function Menu() {
  const [Calc, SetCal] = useState(0);
  return (
    <div>
      <h1>Calculadora del sistema de Movimiento Rectil√≠neo Uniforme (MRU)</h1>
      <button onClick={() => {SetCal(<CalcularT />)}}>CalculadoraT</button>
      <button onClick={() => {SetCal(<CalcularV />)}}>CalculadoraV</button>
      <button onClick={() => {SetCal(<CalcularX />)}}>CalculadoraX</button>
    <div>{Calc}</div>
    <h1>Por: J1SUS:)</h1>
    <h1><a href="https://github.com/J1SUS/">Github</a></h1>
    <h1><a href="https://facebook.com/J1SUS2013/">Facebook</a></h1>
  </div>
  );
}

export default Menu;
